    --clr-secondary: #000000;
    --clr-gray: #dddddd;
    --clr-light: #ffff;
    --clr-white-smoke: #ebebeb;
    --clr-secondary-light: #292929;
  } */


.our-vision {
    background-image: url('https://i.ibb.co/6J6s7dW/side.png');
    background-repeat: no-repeat;
    padding: 44px 0;
  }
  .container-about {
    width: 100%;
    padding-left: 40px;
    padding-right: 20px;
  }
  .flex-about {
    display: flex;

  }
  .container-content {
    padding: 0 90px;
   
  }
  
  .vision-img img {
    border: 5px solid black;
   
    width:650px;
    height:300px;
  }
  .vision-top-content h1 {
    font-size: 4vw;
    font-weight: 700;
    font-family: "Poppins", sans-serif;
  }
  .vision-top-content p {
    font-size: 1.4vw;
    font-weight: 500;
    font-family: "Open Sans", sans-serif;
    color: #292929;
  }
 
  .flex-cont {
    display: flex;
  }
  .vision-bottom-content p {
    font-size: 13px;
    font-weight: 500;
    font-family: "Open Sans", sans-serif;
    color: var(--clr-secondary-light);
    padding: 15px;
  }

  @media(max-width: 900px) {
    .flex-about {
      display: flex;
      flex-direction: column;
    }

    .flex-cont {
      display: flex;
      flex-direction: column;
    }

    .container-content {
      padding: 0 20px;
    }

    .vision-top-content h1 {
      font-size: 2rem;
      font-weight: 700;
      font-family: "Poppins", sans-serif;
    }

    .vision-top-content p {
      font-size: 0.9rem;
      font-weight: 500;
      font-family: "Open Sans", sans-serif;
      color: #292929;
    }

    .vision-bottom-content p {
      font-size: 13px;
      font-weight: 500;
      font-family: "Open Sans", sans-serif;
      color: #292929;
      padding: 4px;
    }
  }