.heading-main-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 100px 0;
}
.heading-main-title h3 {
  font-size: calc(2.5em * 0.625);
  border-bottom: 1px solid #c01120;
  color: #575757;
  padding: 17px 0;
}
.sub-title {
  position: relative;
}
.sub-title::after {
  content: "";
  position: absolute;
  height: 4px;
  width: 30%;
  background: #c01120;
  left: 0;
  bottom: -2px;
}
.heading-main-title h1 {
  font-size: calc(5em * 0.625);
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 4px;
}


.about-sec {
  background-color: white;
}
.about-container {
  padding: 90px 150px;
}
.ab-img img {
  width: 50vw;
  margin: 3em 3em 0;
}
.ab-img {
  padding-right: 10px;
  padding-bottom: 10px;

  background-color: #c01120;
  box-shadow: rgb(0 0 0 / 25%) 0 54px 55px;

  backdrop-filter: blur(5px);
  border-radius: 10px;
  transition: 0.4s ease;
}
.ab-img:hover {
  padding-right: 30px;
  padding-bottom: 30px;

  background-color: #c01120;
  box-shadow: rgb(0 0 0 / 25%) 0 54px 55px;

  backdrop-filter: blur(5px);
  border-radius: 20px;
}
.about-wrap {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
}

.about-content {
  background-image: url("https://i.ibb.co/gd3RdcQ/qoute.png");
  background-repeat: no-repeat;
  background-position: top;
  background-position-x: right;
  background-size: 10vw;
  display: flex;
  flex-direction: column;
  justify-content: center;

  padding-left: 100px;
  padding-right: 40px;
}
.about-content h1 {
  font-size: 40px;
  padding-top: 50px;
}
.about-content p {
  font-size: 20px;
  padding: 40px 0;
}
.button-sq-red {
  font-size: 15px;
  display: block;
  padding: 1em 0.45em;
  text-align: center;
  letter-spacing: 2px;
  color: white;
  width: 25%;
  background: red;
  border-right: 3px solid red;
  text-decoration: none;
  text-transform: uppercase;

  background: linear-gradient(to right, rgb(0, 0, 0) 50%, rgb(204, 48, 48) 50%)
    left;
  background-size: 200%;
  transition: 0.5s ease-out;
}
.button-sq-red:hover {
  background-position: right;
}

@media only screen and (min-device-width: 1025px) and (max-device-width: 1180px) {
  .ab-img img {
    padding-top: 132px;
  }
  .about-wrap {
    justify-content: center;
    align-items: center;
    flex-flow: wrap;
  }
  .button-sq-red {
    width: 50%;
  }
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .ab-img img {
    width: 30vw;
  }
  .about-wrap {
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
  }
  .ab-img img {
    padding-top: 1px;
  }
  .button-sq-red {
    width: 100%;
  }
  .about-container {
    background-position-x: right;
    background-size: 90px;
    display: flex;

    padding-left: 34px;
    padding-right: 0px;
  }
  .about-wrap {
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (min-device-width: 540px) and (max-device-width: 720px) {
  .button-sq-red {
    width: 70%;
  }
  .about-wrap {
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
  }
}
@media only screen and (min-device-width: 769px) and (max-device-width: 1024px) {
  .ab-img img {
    width: 30vw;
  }
  .ab-img img {
    padding-top: 1px;
  }
  .ab-img img {
    width: 300px;
  }
  .about-wrap {
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
  }
  .about-wrap {
    justify-content: center;
    align-items: center;
  }
  .about-content {
    background-position-x: right;
    background-size: 90px;
    display: flex;

    padding-left: 34px;
    padding-right: 0px;
  }
}
@media only screen and (min-device-width: 1200px) and (max-device-width: 1400px) {
  .ab-img img {
    padding-top: 132px;
  }
  .about-wrap {
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
  }
}
@media (max-width: 900px) {

  .about-content p {
    font-size: 16px;
    padding: 40px 0;
  }
  .heading-main-title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px 0;
  }
  .heading-main-title h3 {
    font-size: calc(2em * 0.625);
    border-bottom: 1px solid #c01120;
    color: #575757;
    padding: 17px 0;
  }
  .sub-title {
    position: relative;
  }
  .sub-title::after {
    content: "";
    position: absolute;
    height: 4px;
    width: 30%;
    background: #c01120;
    left: 0;
    bottom: -2px;
  }
  .heading-main-title h1 {
    font-size: calc(3em * 0.625);
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 4px;
  }

  .about-content {
    background-image: url("https://i.ibb.co/gd3RdcQ/qoute.png");
    background-repeat: no-repeat;
    background-position: top;
    background-position-x: right;
    background-size: 10vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
  
    padding-left: 50px;
    padding-right: 40px;
  }
}


