.header-img-serv img {
    width: 100%;
    background-position: center;
    background-size: cover;
  }

  .head-img-text {
    position: absolute;
    top: 22%;
    left: 15%;
    z-index: 100;
  }
  .dash {
    border-top: dashed 3px white;
  }
  .head-img-text h1 {
    font-size: 4vw;
  }

  @media screen and (max-width: 860px) {
    .head-img-text{
      top: 50%;
    }
  }