.btnb {
  background-color:#000000;
  color: var(--clr-light);
  text-decoration: none;
  padding: 10px 20px;
  font-size: 16px;
  text-transform: capitalize;
  display: inline-block;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
  border-radius: 25px;
  z-index: 1;
  margin-bottom: 20px;
}


.btnb::before {
  content: " ";
  position: absolute;
  height: 100%;
  width: 55%;
  background:#c0272d;
  top: 0;
  left: 0;
  border-radius: 5px;
  z-index: -1;
  transition: all 0.3s ease-out;
}

.btnb::after {
  content: " ";
  position: absolute;
  height: 100%;
  width: 55%;
  right: 0;
  top: 0;
  background: #c0272d;
  border-radius: 5px;
  z-index: -1;
  transition: all 0.3s ease-out;
}

.btnb:hover:before {
  width: 0;
}

.btnb:hover:after {
  width: 0;
}

.btnb:hover {
  cursor: pointer;
  color: #ffff;
}

