.MainHeading {
    font-size: 60px;
    font-weight: 900;
    text-transform: uppercase;
  
    font-family: "Poppins", sans-serif;
    text-align: center;
    /* background-color: darkgray; */
  }
  
  .head-main {
    margin: 60px 0;
  }
  
  .SpanColor {
    color: #c0272d;
  }
  
  .spanDesc {
    /* background-color: darkcyan; */
    text-align: center;
  }
  
  .spanDesc p {
    font-size: 1rem;
    margin-bottom: 10px;
    color: #292929;
  }
  
  .dot {
    margin: 0 12px;
    width: 22px;
  }
  
  .divMainHeading {
    display: flex;
    /* background-color: green; */
    height: 20px;
    margin-left: 25%;
  }
  
  .divMainHeadingSubDiv {
    /* background-color: gold;  */
    width: 30%;
    border-top: 3px dashed firebrick;
    margin-top: 9px;
  }
  .section-padding {
    padding: 40px 0;
  }
  .brand-carousel {
    background: linear-gradient(to top, #9b9b9b00, 30%, #47474700);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  .owl-dots {
    text-align: center;
    margin-top: 4%;
  }
  .owl-dot {
    display: inline-block;
    height: 15px !important;
    width: 15px !important;
    background-color: rgb(109, 109, 109) !important;
    opacity: 0.8;
    border-radius: 50%;
    margin: 0 5px;
  }
  .owl-dot.active {
    background-color: #000 !important;
  }
  .accrediation {
    width: 100%;
    padding: 30px;
  }
  .accrediation p {
    padding: 10px;
    font-size: 14px;
    text-align: center;
  }
  .accrediation h4 {
    padding: 5px;
    font-size: 16px;
    font-weight: 700;
    color: #c0272d;
    text-align: center;
  }
  .accre-content {
    background-color: #ebebeb;
    height: 150px;
    width: 18rem;
  }
  